.videoSmall {
  width: 28rem;
  height: 21rem;
  min-width: 28rem;
  min-height: 21rem;
  max-width: 200vh;
  max-height: 90vh;
  margin: .3rem;
  background-color: rgb(0, 0, 0);
  position: relative;
  text-align: center;
}

.fullScreenBtn{
  position: absolute;
  bottom:.5rem;
  right:.5rem;
  font-size: 1.5rem;
  color: white;
}

.nameOnNoVideo{
  color: white;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
}

.nameOnVideo{
  transform: none;
  top: unset;
  bottom: .5rem;
  left: 1rem;
}
