.container{
  width: 30rem;
  height: 40rem;
  background-color: rgba(8, 36, 59, 0.979);
  color: aliceblue;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  opacity: 0.98;
  text-align: center;
  padding-top: 1rem;
}

.closeChat{
  position: absolute;
  right: 2rem;
  top: 1.5rem;
  font-size: 2rem;
}


.chatDiv{
  width: 90%;
  height: 70%;
  background-color: rgba(255, 255, 255, 0.678);
  margin: 2rem auto 0 auto;
  padding-top: 1rem;
  position: relative;
  overflow-y: scroll;
  border: white solid 2px;
  border-radius: 10px;
}

.chat{
  width: 90%;
  height: auto;
  background-color: rgba(8, 36, 59, 0.979);
  position: relative;
  margin: 2rem 1rem;
  padding: 1rem 0;
  border-radius: 5px;
}

.name{
  position: absolute;
  top: .4rem;
  left: 1rem;
  font-size: 1.2rem;
}
.message{
  text-align: left;
  font-size: 1.6rem;
  padding: 2rem;
  overflow-x: auto;
  white-space: pre-wrap;
  white-space: -moz-pre-wrap;
  white-space: -pre-wrap;
  white-space: -o-pre-wrap;
  word-wrap: break-word;
}
.time{
  position: absolute;
  bottom: .4rem;
  right: 1rem;
  font-size: 1.2rem;
}

::-webkit-scrollbar {
  width: 7px;
}

::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey; 
  border-radius: 10px;
}

::-webkit-scrollbar-thumb {
  background: rgba(8, 36, 59, 0.795); 
  border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover {
  background: rgba(8, 36, 59, 0.979); 
}

.chatFormDiv{
  margin-top: 1rem;
}

.chatForm{
  width: 90%;
  margin: auto;
  padding: 1rem 0;
  display: flex;
  flex-direction: row;
  margin-right: 1rem;
}

.chatInput{
  width: 80%;
  padding: 0.9rem;
  border-radius: 5px;
  resize: none;
  height: auto;
}

.chatInput:focus{
  background-color: rgb(198, 221, 240);
  outline: none;
}

.chatSubmitBtn{
  width: 100%;
  color: white;
  font-size: 2rem;
  margin: auto;
}

