.container{
  width: 30rem;
  height: 40rem;
  background-color: rgba(8, 36, 59, 0.979);
  color: aliceblue;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  opacity: 0.98;
  text-align: center;
  padding-top: 1rem;
}

.closeChat{
  position: absolute;
  right: 2rem;
  top: 1.5rem;
  font-size: 2rem;
}

@media screen and (min-width: 320px) and (max-width: 767px) and (orientation: landscape) {
  .container{
    transform: rotate(-90deg) translate(-50%,-50%);
    transform-origin: left top;
    /* overflow-x: hidden; */
  }
}

.usersDiv{
  width: 90%;
  height: 85%;
  background-color: rgba(255, 255, 255, 0.678);
  margin: 2rem auto 0 auto;
  padding-top: 1rem;
  position: relative;
  overflow-y: scroll;
  border: white solid 2px;
  border-radius: 10px;
}

.user{
  width: 90%;
  height: 4rem;
  background-color: rgba(8, 36, 59, 0.979);
  position: relative;
  margin: 2rem 1rem;
  padding: 1rem 0;
  border-radius: 5px;
}

.name{
  text-align: left;
  font-size: 1.6rem;
  padding-left: 1rem;
}
.audio{
  font-size: 1.6rem;
  position: absolute;
  right: 1rem;
  bottom: 1rem;
}
.video{
  font-size: 1.6rem;
  position: absolute;
  right: 4rem;
  bottom: 1rem;
}

::-webkit-scrollbar {
  width: 7px;
}

::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey; 
  border-radius: 10px;
}

::-webkit-scrollbar-thumb {
  background: rgba(8, 36, 59, 0.795); 
  border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover {
  background: rgba(8, 36, 59, 0.979); 
}